"use client";

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { getPurchaseOrders } from '@/lib/services/purchaseService';
import { PurchaseOrder } from '@/models/types';

export default function PurchaseOrdersPage() {
  const [purchaseOrders, setPurchaseOrders] = useState<PurchaseOrder[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  
  // Filter state
  const [filterStatus, setFilterStatus] = useState<string>('');
  const [dateRange, setDateRange] = useState<{start: string, end: string}>({
    start: '',
    end: ''
  });

  // Load purchase orders
  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      setError(null);
      
      try {
        const orders = await getPurchaseOrders(
          filterStatus as PurchaseOrder['status'] || undefined
        );
        setPurchaseOrders(orders);
      } catch (err: any) {
        console.error('Error fetching purchase orders:', err.message || JSON.stringify(err));
        setError(`حدث خطأ أثناء تحميل طلبات الشراء: ${err.message || 'خطأ غير معروف'}. الرجاء المحاولة مرة أخرى.`);
      } finally {
        setLoading(false);
      }
    };

    fetchData();
  }, [filterStatus]);

  // Filter by date range
  const filteredOrders = purchaseOrders.filter(order => {
    let includeOrder = true;
    
    // Apply date filters if they exist
    if (dateRange.start) {
      includeOrder = includeOrder && new Date(order.order_date) >= new Date(dateRange.start);
    }
    
    if (dateRange.end) {
      includeOrder = includeOrder && new Date(order.order_date) <= new Date(dateRange.end);
    }
    
    return includeOrder;
  });

  // Calculate statistics
  const pendingCount = purchaseOrders.filter(order => 
    order.status === 'draft' || order.status === 'submitted'
  ).length;
  
  const receivedCount = purchaseOrders.filter(order => 
    order.status === 'received'
  ).length;
  
  const completedCount = purchaseOrders.filter(order => 
    order.status === 'completed'
  ).length;

  // Monthly purchase orders (group by month)
  const currentDate = new Date();
  const currentMonth = currentDate.getMonth();
  const currentYear = currentDate.getFullYear();
  
  const thisMonthOrders = purchaseOrders.filter(order => {
    const orderDate = new Date(order.order_date);
    return orderDate.getMonth() === currentMonth && 
           orderDate.getFullYear() === currentYear;
  });

  return (
    <div className="container mx-auto">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold dark:text-white">طلبات الشراء</h1>
        <div className="flex gap-3">
          <Link href="/purchase-orders/monthly" className="btn btn-secondary">
            إنشاء طلب شهري
          </Link>
          <Link href="/purchase-orders/new" className="btn btn-primary">
            إنشاء طلب شراء
          </Link>
        </div>
      </div>

      {/* Statistics Cards */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-6 mb-8">
        <div className="card bg-white dark:bg-gray-800">
          <h3 className="text-lg font-medium text-gray-700 dark:text-gray-300">إجمالي الطلبات</h3>
          <p className="mt-2 text-3xl font-bold dark:text-white">{purchaseOrders.length}</p>
        </div>
        
        <div className="card bg-yellow-50 border-r-4 border-yellow-500 dark:bg-yellow-900/20 dark:border-yellow-600">
          <h3 className="text-lg font-medium text-gray-700 dark:text-gray-300">طلبات معلقة</h3>
          <p className="mt-2 text-3xl font-bold dark:text-yellow-300">{pendingCount}</p>
        </div>
        
        <div className="card bg-blue-50 border-r-4 border-blue-500 dark:bg-blue-900/20 dark:border-blue-600">
          <h3 className="text-lg font-medium text-gray-700 dark:text-gray-300">تم الاستلام</h3>
          <p className="mt-2 text-3xl font-bold dark:text-blue-300">{receivedCount}</p>
        </div>
        
        <div className="card bg-green-50 border-r-4 border-green-500 dark:bg-green-900/20 dark:border-green-600">
          <h3 className="text-lg font-medium text-gray-700 dark:text-gray-300">مكتملة</h3>
          <p className="mt-2 text-3xl font-bold dark:text-green-300">{completedCount}</p>
        </div>
      </div>

      {error && (
        <div className="bg-red-100 border-r-4 border-red-500 text-red-700 p-4 mb-6 rounded-md dark:bg-red-900/30 dark:border-red-800 dark:text-red-400">
          <p>{error}</p>
        </div>
      )}

      {/* Filters */}
      <div className="bg-white shadow rounded-lg p-6 mb-6 dark:bg-gray-800">
        <h2 className="text-lg font-semibold mb-4 dark:text-white">تصفية طلبات الشراء</h2>
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div>
            <label className="block mb-2 text-sm font-medium dark:text-gray-300">الحالة</label>
            <select
              value={filterStatus}
              onChange={(e) => setFilterStatus(e.target.value)}
              className="select w-full dark:bg-gray-700 dark:border-gray-600 dark:text-white"
            >
              <option value="">جميع الحالات</option>
              <option value="draft">مسودة</option>
              <option value="submitted">مقدم</option>
              <option value="approved">معتمد</option>
              <option value="ordered">تم الطلب</option>
              <option value="received">تم الاستلام</option>
              <option value="completed">مكتمل</option>
            </select>
          </div>
          
          <div>
            <label className="block mb-2 text-sm font-medium dark:text-gray-300">من تاريخ</label>
            <input
              type="date"
              value={dateRange.start}
              onChange={(e) => setDateRange({ ...dateRange, start: e.target.value })}
              className="input w-full dark:bg-gray-700 dark:border-gray-600 dark:text-white"
            />
          </div>
          
          <div>
            <label className="block mb-2 text-sm font-medium dark:text-gray-300">إلى تاريخ</label>
            <input
              type="date"
              value={dateRange.end}
              onChange={(e) => setDateRange({ ...dateRange, end: e.target.value })}
              className="input w-full dark:bg-gray-700 dark:border-gray-600 dark:text-white"
            />
          </div>
        </div>
      </div>

      {/* Monthly Summary */}
      <div className="bg-white shadow-md rounded-lg p-6 mb-6 dark:bg-gray-800">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-lg font-semibold dark:text-white">طلبات الشهر الحالي ({thisMonthOrders.length})</h2>
          {thisMonthOrders.length > 0 && (
            <Link href={`/reports/monthly/${currentYear}/${currentMonth + 1}`} className="text-primary hover:underline dark:text-blue-400">
              عرض التقرير الشهري
            </Link>
          )}
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {thisMonthOrders.slice(0, 3).map(order => (
            <div key={order.id} className="border rounded-md p-4 hover:bg-gray-50 dark:border-gray-700 dark:hover:bg-gray-700">
              <div className="flex justify-between items-center mb-2">
                <h3 className="font-medium dark:text-white">طلب #{order.id.substring(0, 8)}</h3>
                <StatusBadge status={order.status} />
              </div>
              <p className="text-gray-600 text-sm mb-2 dark:text-gray-400" dir="ltr">
                {new Date(order.order_date).toLocaleDateString('ar-SA')}
              </p>
              <p className="text-gray-700 dark:text-gray-300">
                {order.total_amount ? `${order.total_amount} ريال` : 'المبلغ غير محدد'}
              </p>
              <div className="mt-3">
                <Link href={`/purchase-orders/${order.id}`} className="text-primary hover:underline text-sm dark:text-blue-400">
                  عرض التفاصيل
                </Link>
              </div>
            </div>
          ))}
          
          {thisMonthOrders.length === 0 && (
            <div className="col-span-3 bg-gray-50 p-4 rounded-md text-center text-gray-500 dark:bg-gray-700 dark:text-gray-400">
              لا توجد طلبات شراء لهذا الشهر
            </div>
          )}
        </div>
      </div>

      {/* Purchase Orders Table */}
      <div className="bg-white shadow-md rounded-lg overflow-hidden dark:bg-gray-800">
        <div className="p-4 bg-gray-50 border-b dark:bg-gray-700 dark:border-gray-600">
          <h2 className="font-semibold text-lg dark:text-white">جميع طلبات الشراء</h2>
        </div>
        
        {loading ? (
          <div className="text-center py-10 dark:text-gray-300">
            <p className="text-lg">جاري تحميل البيانات...</p>
          </div>
        ) : filteredOrders.length === 0 ? (
          <div className="text-center py-10">
            <p className="text-lg text-gray-500 dark:text-gray-400">لا توجد طلبات شراء مطابقة للمعايير المحددة.</p>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700">
              <thead className="bg-gray-50 dark:bg-gray-700">
                <tr>
                  <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider dark:text-gray-300">
                    رقم الطلب
                  </th>
                  <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider dark:text-gray-300">
                    التاريخ
                  </th>
                  <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider dark:text-gray-300">
                    الحالة
                  </th>
                  <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider dark:text-gray-300">
                    المبلغ الإجمالي
                  </th>
                  <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider dark:text-gray-300">
                    المورد
                  </th>
                  <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider dark:text-gray-300">
                    منشئ الطلب
                  </th>
                  <th className="px-4 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider dark:text-gray-300">
                    إجراءات
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200 dark:bg-gray-800 dark:divide-gray-700">
                {filteredOrders.map((order) => (
                  <tr key={order.id} className="hover:bg-gray-50 dark:hover:bg-gray-700">
                    <td className="px-4 py-4 whitespace-nowrap dark:text-gray-300">
                      #{order.id.substring(0, 8)}
                    </td>
                    <td className="px-4 py-4 whitespace-nowrap dark:text-gray-300" dir="ltr">
                      {new Date(order.order_date).toLocaleDateString('ar-SA')}
                    </td>
                    <td className="px-4 py-4 whitespace-nowrap">
                      <StatusBadge status={order.status} />
                    </td>
                    <td className="px-4 py-4 whitespace-nowrap dark:text-gray-300">
                      {order.total_amount ? `${order.total_amount} ريال` : 'غير محدد'}
                    </td>
                    <td className="px-4 py-4 whitespace-nowrap dark:text-gray-300">
                      {order.vendor || 'غير محدد'}
                    </td>
                    <td className="px-4 py-4 whitespace-nowrap dark:text-gray-300">
                      {order.created_by}
                    </td>
                    <td className="px-4 py-4 whitespace-nowrap text-sm">
                      <Link 
                        href={`/purchase-orders/${order.id}`}
                        className="text-secondary hover:underline ml-2 dark:text-purple-400"
                      >
                        عرض
                      </Link>
                      
                      {(order.status === 'draft' || order.status === 'submitted') && (
                        <Link 
                          href={`/purchase-orders/${order.id}/edit`}
                          className="text-primary hover:underline ml-2 dark:text-blue-400"
                        >
                          تحرير
                        </Link>
                      )}
                      
                      {order.status === 'ordered' && (
                        <Link 
                          href={`/purchase-orders/${order.id}/receive`}
                          className="text-accent hover:underline dark:text-teal-400"
                        >
                          استلام
                        </Link>
                      )}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  );
}

// Helper component for status badges
function StatusBadge({ status }: { status: PurchaseOrder['status'] }) {
  const statusColors: Record<PurchaseOrder['status'], { bg: string; text: string; darkBg: string; darkText: string; label: string }> = {
    draft: { 
      bg: 'bg-gray-100', 
      text: 'text-gray-800', 
      darkBg: 'dark:bg-gray-700', 
      darkText: 'dark:text-gray-300', 
      label: 'مسودة' 
    },
    submitted: { 
      bg: 'bg-blue-100', 
      text: 'text-blue-800', 
      darkBg: 'dark:bg-blue-900/30', 
      darkText: 'dark:text-blue-300', 
      label: 'مقدم' 
    },
    approved: { 
      bg: 'bg-green-100', 
      text: 'text-green-800', 
      darkBg: 'dark:bg-green-900/30', 
      darkText: 'dark:text-green-300', 
      label: 'معتمد' 
    },
    ordered: { 
      bg: 'bg-purple-100', 
      text: 'text-purple-800', 
      darkBg: 'dark:bg-purple-900/30', 
      darkText: 'dark:text-purple-300', 
      label: 'تم الطلب' 
    },
    received: { 
      bg: 'bg-yellow-100', 
      text: 'text-yellow-800', 
      darkBg: 'dark:bg-yellow-900/30', 
      darkText: 'dark:text-yellow-300', 
      label: 'تم الاستلام' 
    },
    completed: { 
      bg: 'bg-green-100', 
      text: 'text-green-800', 
      darkBg: 'dark:bg-green-900/30', 
      darkText: 'dark:text-green-300', 
      label: 'مكتمل' 
    },
  };

  const { bg, text, darkBg, darkText, label } = statusColors[status] || { 
    bg: 'bg-gray-100', 
    text: 'text-gray-800',
    darkBg: 'dark:bg-gray-700',
    darkText: 'dark:text-gray-300', 
    label: status 
  };

  return (
    <span className={`${bg} ${text} ${darkBg} ${darkText} px-2 py-1 rounded-full text-xs`}>
      {label}
    </span>
  );
}